<template>
  <f7-page>
    <f7-navbar back-link="Back" title="Timeline Vertical" sliding>
       <f7-nav-right>
                <f7-link open-popup="#demo-popup"><f7-icon f7="add_round"></f7-icon></f7-link>
              </f7-nav-right>
    </f7-navbar>

    
    <f7-block-title>Rich Content</f7-block-title>
    <f7-timeline>
      <f7-timeline-item :day="event.start"
        v-for="event in events"
        :month="event.month"
        :time="event.hour + ':' + event.minute"
        :title="event.title"
      >
        <f7-list inset>
          <f7-list-item :link="'/detail-task/'+event.id" :id="'event' + event.id" :class="'color-white ' + event.class">
            <f7-timeline-item-child 
              :subtitle="event.note"
              :text="event.content">
          
        </f7-timeline-item-child>
          </f7-list-item>
        </f7-list>
      </f7-timeline-item>
    </f7-timeline>
    <f7-popup id="demo-popup" no-navbar>
         <f7-list form id="my-form">
        <f7-list-item>
         <div>
            <label for="">Tiêu đề: </label>
            <f7-input name="title" type="text"/>
         </div>
        </f7-list-item>
        <f7-list-item>
         <div>
            <label for="">Nội dung: </label>
            <f7-input name="content" type="text"/>
         </div>
        </f7-list-item>
        <f7-list-item>
         <div>
            <label for="">Lưu ý </label>
            <f7-input name="Note" type="text" />
         </div>
        </f7-list-item>
        <f7-list-item>
         <div>
            <label for="">Ngày bắt đầu </label>
            <f7-input name="start" type="text" />
         </div>
        </f7-list-item>
        <f7-list-item>
         <div>
            <label for="">Ngày kết thúc </label>
            <f7-input name="end" type="text"/>
         </div>
        </f7-list-item>
        <f7-buttons >
                <f7-button class="button" close-popup color="red">Cancel</f7-button>
                <f7-button class="button" @click="createTask()" color="green">Confirm</f7-button>
            </f7-buttons>
      </f7-list>
    </f7-popup>
  </f7-page>
</template>
<script>
var $$ = Dom7;
var myApp = new Framework7();
import {cf} from './../../main.js'
  export default {
    data: function(){
      return{
        events: []
      }
    },
    methods: {
      fetchEvents(){
        this.$http.post(cf.serverURL + 'list-work-list',{
          user_id: localStorage.getItem('id'),
          token: localStorage.getItem('token'),
          worker_id: localStorage.getItem('id')
        }).then(
          function(res){
            // console.log(res);
            var self = this;
            this.events = res.body.results;
            var now = new Date();
            $$.each(this.events, function(value, key){
              var n = new Date(key.date_start);
              var e = new Date(key.date_end);
              key.start = n.getDate();
              key.minute = n.getMinutes();
              key.hour = n.getHours();
              key.month = n.getMonth();
              if(key.status !=2 && e < now){
                key.class="bg-red"
              }
              else if(key.status == 0){
                key.class="color-black";
              }
              else if(key.status == 1){
                key.class="bg-blue"
              }
              else if(key.status == 2){
                key.class ="bg-green"
              }
            })
          },
          function(res){

          }
        )
      },
      createTask(){
        var self = this;
        var formData = myApp.formToData("#my-form");
        console.log(formData);
        this.$http.post(cf.serverURL + 'create-work-list',
          {
            user_id: localStorage.getItem('id'),
            title: formData.title,
            content: formData.content,
            note: formData.note,
            date_start: formData.start,
            date_end: formData.end,
            created_by: localStorage.getItem('id'),
            token: localStorage.getItem('token')
          }
        ).then(
          function(res){
            self.$f7.views.main.router.refreshPage();
            myApp.closeModal('#demo-popup');
            self.$f7.alert("Created Successfully","Create Task");
          },
          function(res){
            console.log(res);
          })
      }
    },
    created: function(){

      this.fetchEvents();
      myApp.showPreloader();
    },
    updated: function(){
      myApp.hidePreloader();
    }
  }
</script>
<style scoped>
  .timeline-item-subtitle{
    font-size: 15px;
    color:#fff;
  }
  .timeline-item-text{
    font-size: 20px;
  }
  .border-red{
    border: 2px solid red !important;
  }
</style>


