<template>
      <f7-page @page:afterback="back" data-page="page1">
          <f7-navbar title="Task" back-link="Back" sliding>
               <f7-nav-right>
                <f7-link link="/about/"><f7-icon f7="add"></f7-icon></f7-link>
              </f7-nav-right>
          </f7-navbar>
          <f7-toolbar tabbar labels>
              <f7-link tab-link="#tab1"><f7-icon f7="timeline"></f7-icon>Waiting</f7-link>
              <f7-link tab-link="#tab2"><f7-icon f7="reload"></f7-icon>Doing</f7-link>
              <f7-link tab-link="#tab3"><f7-icon f7="check"></f7-icon>Done</f7-link>
              <f7-link tab-link="#tab4"><f7-icon f7="info"></f7-icon>Overdue</f7-link>
            </f7-toolbar>
          <f7-tabs>
      <f7-tab id="tab1" active>
        <f7-list>
          <f7-list-item :link="'/detail-task/' + task.id" v-for="task in tasks" v-if="task.status == 0">{{task.title}}</f7-list-item>
        </f7-list>
      </f7-tab>
      <f7-tab id="tab2">
        <f7-list>
          <f7-list-item :link="'/detail-task/' + task.id" v-for="task in tasks" v-if="task.status == 1">{{task.title}}</f7-list-item>
        </f7-list>
        </f7-list>
      </f7-tab>
      <f7-tab id="tab3">
        <f7-list>
          <f7-list-item :link="'/detail-task/' + task.id" v-for="task in tasks" v-if="task.status == 2">{{task.title}}</f7-list-item>
        </f7-list>
        </f7-list>
      </f7-tab>
      <f7-tab id="tab4">
        <f7-list>
          <f7-list-item :link="'/detail-task/' + task.id" v-for="task in tasks" v-if="task.status !=2" :key="task.id">{{task.title}}</f7-list-item>
        </f7-list>
        </f7-list>
      </f7-tab>
    </f7-tabs>
      </f7-page>        
  </template>
<script>
  import {cf} from './../../main.js';
  export default{
    data: function(){
      return{
        tasks: [],
        now: Date.now()
      }
    },
    methods: {
      fetchTasks(){
        var self = this;
        this.$http.post((cf.serverURL + 'list-work-list'),{user_id: localStorage.getItem('id'), token: localStorage.getItem('token'), worker_id: this.$route.params.id})
        .then(
          function(res){
            this.tasks = res.body.results;
          },
          function(res){
            alert(res);
          }
        )
      },
      back(){
      }
    },
    mounted: function(){
      this.fetchTasks();
    }
  }
</script>
<style scoped>
  label{
    color: #007aff;
    font-weight: bold;
    font-size: 16px;
  }
  p{
    font-size: 20px;
  }
</style>


